.main {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 10px;
}

.content {
  display: flex;
  justify-content: space-between;
  flex: auto;
}

.prices {
  flex: auto;
}

.title {
  font-size: 2.8rem;
  font-weight: 900;
  text-transform: uppercase;
  color: var(--color-title);
  padding: 0;
  margin: 0;
  margin-bottom: 20px;
}

.prices__description {
  padding: 0;
  margin: 0;
  margin-bottom: 20px;
}

.prices__img {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  margin-bottom: 42px;
}

.prices__block {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 5px;

  img {
    margin-bottom: 10px;
  }
}

.prices__text {
  font-weight: 700;
  color: var(--color-text-prices-img);
  padding: 0;
  margin: 0;
}


// form

.form__prices {
  display: flex;
  flex-direction: column;
  width: 52rem;
  padding-left: 2rem;
  margin-bottom: 23px;
}

//currency

.form__select {
  display: flex;
  align-items: center;
  flex: auto;
  font-size: 1.4rem;
  margin-bottom: 20px;
}

.form__text {
  padding: 0;
  margin: 0;
  margin-right: auto;
  color: var(--color-title);
}

.currrency {
  display: flex;
  justify-content: space-between;
  flex-basis: 350px;
  text-transform: uppercase;
  color: var(--color-text-main);
  padding: .5rem 2rem;
  background-color: var(--color-input-bg);
  border: 1px solid var(--color-input-border);
  border-radius: 2rem;
}



.currrency__group {
  display: flex;
  border: 1px solid transparent;
  border-radius: 2rem;
  transition: all 0.5s ease;

  &:not(:last-child) {
    margin-right: 4.2rem;
  }
}

.currrency__input {
  opacity: 0;
  position: absolute;
}

.currrency__input+label {
  flex: 1 1 100%;
  cursor: pointer;
  z-index: 1;
  transition: all 0.5s ease;
}

.currrency__input:checked+label,
.currrency__group:hover {
  position: relative;
  color: var(--color-title);
  background-color: var(--color-white);
  transition: all 0.5s ease;

  &::before {
    position: absolute;
    content: "";
    display: block;
    top: 0;
    left: 0;
    transform: translate(-2.1rem, -0.7rem);
    padding: 1.4rem 3.5rem;
    border: 1px solid var(--color-text-accent);
    border-radius: 2rem;
    transition: all 0.5s ease;
  }
}


// service

.form__input,
.choices__inner {
  display: flex;
  justify-content: space-between;
  min-width: 367px;
  min-height: 0;
  text-transform: uppercase;
  color: var(--color-text-main);
  padding: .6rem 2rem;
  background-color: var(--color-input-bg);
  border: 1px solid var(--color-input-border);
  border-radius: 2rem;
}

.is-open .choices__list--dropdown {
  background-color: #ffffff;
  border: 1px solid var(--color-input-border);
  border-bottom-left-radius: 2rem;
  border-bottom-right-radius: 2rem;
}

.is-open .choices__inner,
.form__input:focus {
  outline: none;
  border: 1px solid var(--color-input-border);
}


.is-open .choices__inner {
  border-radius: 2rem 2rem 0 0;
}



.is-focused .choices__inner {
  outline: none;
  border: 1px solid var(--color-input-border);
}


.form__select select::-ms-expand {
  display: none;
}

.choices__list--single {
  padding: 0px 16px 0px 0px;
}

.choices[data-type*='select-one'] .choices__inner {
  padding-bottom: 6px;
}

.choices[data-type*='select-one']:after {
  content: '';
  width: 9px;
  height: 9px;
  border-top: 2px solid var(--color-text-accent);
  border-right: 2px solid var(--color-text-accent);
  transform: rotate(135deg);
  position: absolute;
  right: 14.5px;
  top: 38%;
  margin-top: -2.5px;
  pointer-events: none;
}

//   calc services prices

.services {
  display: flex;
  flex-direction: column;
  font-size: 1.4rem;
  margin-right: 50px;
  margin-bottom: 50px;
}

.services__wrap {
  display: flex;
  flex-direction: column;
  padding: 10px;
  margin-bottom: 50px;
  border-radius: 2px;
  box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.1);
}

.servisec,
.services__types--popular,
.services__types--other {
  display: flex;

}

.services__vertical {
  display: flex;
  border-radius: 10px;
  background-color: var(--color-popular);
  color: var(--color-text-popular);
  padding: 10px;
  margin-right: 10px;
}

.services__vertical--text {
  writing-mode: tb-rl;
  transform: rotate(180deg);
  text-align: center;
}


.services__vertical--other {
  background-color: var(--color-other);
  color: var(--color-text-other);

}

.vertical-text {
  transform: rotate(270deg);
}



.servises__table {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  overflow: auto;
}

.table__title,
.table-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px;
  border-radius: 10px;

}


.table__title {
  color: var(--color-black);
  font-weight: 700;
}

.table-row:nth-of-type(even) {
  background-color: var(--color-table-bg);

}


.table__title-cell {
  padding: 20px 15px;
}


.tabl-row__group {
  font-weight: 700;
  padding: 20px;
  transition: color 0.2s ease;

  label {
    cursor: pointer;
    z-index: 2;
  }
}

.table-row__text {
  flex-basis: 280px;
}


.table__number {
  display: flex;
}

.tabl-row__input {
  opacity: 0;
  position: absolute;
}


.tabl-row__input:checked~label {
  color: var(--color-text-popular);
  position: relative;
  transition: all 0.3s ease;

  &::before {
    position: absolute;
    display: block;
    content: "";
    background-color: var(--color-popular);
    top: 0;
    left: 0;

    padding: 2.8rem 4.4rem;
    border-radius: 1rem;
    transform: translate(-2rem, -1.8rem);
    z-index: -1;
    transition: all 0.3s ease;
  }
}

.total-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.total-price {
  display: inline-flex;
  align-items: center;
  color: var(--color-title);
}

.total-price__title {
  font-size: 1.6rem;
  font-weight: 700;
  padding-right: 2rem;
}

.total-price__info,
.total-price__info--input {
  font-size: 2.4rem;
  font-weight: 700;
}

.total-price__info--input {
  color: var(--color-title);
  padding-right: 2rem;
  max-width: 11rem;
  border: none;
}

.focus-visible {
  outline: 0;
}

.services__btn {
  display: inline-flex;
  font-size: 1.8rem;
  font-weight: 700;
  text-transform: uppercase;
  color: var(--color-white);
  background-color: var(--color-btn-bg);
  padding: 1.5rem 7.5rem;
  border-radius: 4rem;
  box-shadow: 0px 5px 10px 0px var(--color-shadow-btn);
}
